.coinStructuredFundDetail{
  overflow: hidden;
  padding-bottom: 20rem;
  .detailtitle {
    margin: 0 1.5rem;
    font-size: 1.6rem;
    padding-bottom: 1rem;
    font-weight: 600;
    color: #121212;
    border-bottom: 1px solid rgba(221, 221, 230, 0.1);
  }
  .canGet,
  .noCanGet {
    padding: 1.2rem 0;
    text-align: center;
    border-radius: 8px 8px 8px 8px;
  }
  .noCanGet {
    background: #808191;
    border: 1px solid #808191;
    color: #b5b5be;
  }
  .canGet {
    background: transparent;
    border: 1px solid #1e75f9;
    color: #1e75f9;
  }
    
  .getbacktips{
    padding:0 2.8rem;
    text-align: left;
    font-size: 1.4rem;
    color: #FF24AB;
  }
  .footertext {
    box-sizing: border-box;
    font-size: 1rem;
    position: fixed;
    bottom: 0rem;
    left: 0;
    padding: 1.6rem 1rem 4rem;
    width: 100%;
    text-align: center;
    background: #fff;
    .footerdynamic {
      font-size: 1.8rem;
      padding: 0 2.8rem 1.2rem;
      color: #98989E;
    }
    .footercolors {
      font-weight: bold;
      color: #121212;
    }

  }
  .nodetail {
    font-size: 1.6rem;
  }
}
.detailPopup {
  background: #000;
  color: #fff;
  padding: 1rem 1.5rem 3rem;
  &-title {
    padding-bottom: 1rem;
    border-bottom: 1px solid rgba(221, 221, 230, 0.1);
    margin-bottom: 2.5rem;
    &-main {
      font-size: 1.6rem;
    }
    &-close {
      font-size: 1.2rem;
      color: #808191;
    }
  }

  &-content {
    font-size: 1.2rem;
    margin-bottom: 0.8rem;
  }
  &-content-title {
    font-size: 1.4rem;
  }
  .mb-20 {
    margin-bottom: 2rem;
  }
  .colorGary {
    color: #98989e;
  }
  .submitcbutton {
    padding: 1.2rem 0;
    text-align: center;
    font-size: 1.4rem;
    background: #0046ff;
    border-radius: 8px 8px 8px 8px;
  }
  .set{
    width: 1.2rem;
    height: 1.2rem;
  }
  .popupSlider{
    width: 20rem;
    height: 5rem;
    color: #fff;
    margin: 0 0 0 auto;
    .za-slider__mark{
      color: #fff;
      font-size: 1.2rem;
    }
    .za-slider__line .za-slider__line__bg{
      background: #0046ff;
    }
  }

}
