.welcomeModal {
  padding: 1.5rem;
  padding-top: 3rem;
  display: flex;
  flex-direction: column;
  .modaltitle {
    width: 4rem;
  }
  .popupTitle{
    font-size: 3rem;
    color: #121212;
    font-weight: 600;
  }
  .modalLabal {
    font-size: 1.6rem;
    font-weight: 500;
    color: #121212;
    margin-bottom: 1.6rem;
  }
  .modalAddress {
    font-size: 1.6rem;
    padding: 1.6rem;
    color: #121212;
    border-radius: 1.2rem;
    border: 1px solid #BEBEBE;
    margin-bottom: 2.4rem;
  }
  .modalInput {
    background: #F0F0F7;
    border-radius: 1.2rem;
    padding: 1.6rem 1.4rem;
    font-size: 1.4rem;
    border: 1px solid rgba(0, 0, 0, 0);
    margin-bottom: 1rem;
  }
  .redcolorText {
    font-size: 1.2rem;
    color: #FF0E1A;
    text-align: right;
    margin-bottom: 5rem;
  }

  .yellowcolorText {
    font-size: 1.2rem;
    color: #A8A8BC;
    text-align: center;
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
  }
}
