.button-common{
  background: #0046FF;
  border-radius:0.8rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.4rem;
  font-weight: 500;
  color: #FFFFFF;
}

.button-click {
  transition: opacity 0.2s;
  outline: none;
  position: relative;
}

.button-click::before {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  background-color: #000;
  border: inherit;
  border-color: #000;
  border-radius: inherit;
  transform: translate(-50%, -50%);
  opacity: 0;
  content: ' ';
}

.button-click:active::before {
  opacity: 0.3;
}

