.buyContent{
  padding-bottom: 10rem;
}
.coin-buy-limit {
  border-radius: 0.8rem;
  border: 1px solid #B5B5BE;
  padding: 1.2rem 0;
}

.coin-buy-limit-experience {
  width: 10rem;
  height: 2.5rem;
  border-radius: 0.4rem;
  background-color: rgba(255, 132, 76, 0.1);
  font-size: 1.2rem;
  font-weight: 400;
  color: #FF844C;
}

.coin-buy-limit-nft {
  overflow: auto;
  transition: height .3s;

  .coin-buy-limit-nft-item {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 4rem;
    background-color: rgba(128, 129, 145, 0.06);
    padding: 0 0.8rem;
    margin-left: 1rem;
    margin-right: 1rem;
    margin-top: 1rem;
  }
}

.coin-buy-cost {
  width: 7rem;
  height: 3.2rem;
  font-size: 1.4rem;
  font-weight: bold;
  color: #98989E;
  border: 1px solid #98989E;
  border-radius: 0.6rem;
}

.coin-submit-buy{
  width: 58px;
  height: 27px;
  border-radius: 4px;
  border: 1px solid #5997EE;
  font-size: 12px;
  font-weight: 400;
  color: #5997EE;
}
.coin-reline{
  width: 58px;
  height: 27px;
  border-radius: 4px 4px 4px 4px;
  border: 1px solid #B5B5BE;
  font-size: 12px;
  font-weight: 400;
  color: #B5B5BE;
}

.add_limit{
  padding: .6rem .8rem;
  background: #FF0F6F;
  border-radius: 8px 8px 8px 8px;
  color: #fff;
  font-size: 1.2rem;
}
.buyfenInput{
  width: calc(100% - 2.4rem);
  padding: 1.4rem 1.2rem;
  background: #F0F0F7;
  border-radius: 12px 12px 12px 12px;
  color: #8E8E93;
}