.tabs {
  display: flex;
  font-size: 1.5rem;
  height: 4.6rem;
  background: #15172A;
  border-radius: 12px 12px 0 0;
  .show-item {
    flex: 1;
    background: #282C4A;
    line-height: 2.6rem;
    height: 2.6rem;
    text-align: center;
    border-radius: 12px 12px 0 0;
    font-weight: 600;
    color:#FFFFFF ;
  }
  .hide-item {
    color: #9395A4;
    flex: 1;
    height: 2.6rem;
    text-align: center;
    line-height: 2.6rem;
    border-radius: 12px 12px 0 0;
  }
}
