html {
    font-size: 62.5%;
    -webkit-font-smoothing: antialiased;
}

body {
    font-size: 14px;

}

html body {
    min-height: 100vh;
    font-family: PingFang SC-Medium, PingFang SC, serif;
    background-size: auto auto;
    background-color: #eee;
    /*background-repeat: no-repeat;*/
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video, img {
    margin: 0;
    padding: 0;
    border: 0;
}

a {
    display: inline-block;
    text-decoration: none;
    color: #262626;
}

.icon {
    width: 1em;
    height: 1em;
    vertical-align: -0.15em;
    fill: currentColor;
    overflow: hidden;
}

@media screen and (min-width: 1024px) {
    body {
        width: 375px;
        margin-left: 50% !important;
        left: -187px;
        position: absolute;
        border: #fff solid 1px;
    }
}


input::-webkit-search-cancel-button {
    display: none;
  }

input[type="search"]::-ms-clear {
    display: none;
}
input{
    border: none;
}
.fl-row {
    display: flex;
    flex-direction: row;
    align-items: center;
}
.fl-column {
    display: flex;
    flex-direction: column;
}


:root:root {
    --rv-selector-checked-color: rgba(0, 120, 249, 0.2);
    --rv-primary-color: #007af9;
    
}
